@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);


.Cube {
    width: 50px;
    height: 50px;
    margin: 20px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    pointer-events: none;
}





.Side {
    background-color: rgba(252, 252, 142);
    opacity: .5;
    width: 50px;
    height: 50px;
    border: rgb(0, 58, 0) solid 1px;
    border-radius: 5px;
    position: absolute;
    pointer-events: all;
}

.Cube .Side:nth-child(1) {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
}

.Cube .Side:nth-child(2) {
    -webkit-transform: rotateX(90deg) translateZ(25px) translateY(25px);
            transform: rotateX(90deg) translateZ(25px) translateY(25px)
}

.Cube .Side:nth-child(3) {
    -webkit-transform: translateZ(50px);
            transform: translateZ(50px);
}

.Cube .Side:nth-child(4) {
    -webkit-transform: rotateX(90deg) translateZ(-25px) translateY(25px) ;
            transform: rotateX(90deg) translateZ(-25px) translateY(25px) ;
}

.Cube .Side:nth-child(5) {
    -webkit-transform: translateZ(25px) translateX(25px) rotateY(90deg);
            transform: translateZ(25px) translateX(25px) rotateY(90deg);
}
  
.Cube .Side:nth-child(6) {
    -webkit-transform: translateZ(25px) translateX(-25px) rotateY(-90deg);
            transform: translateZ(25px) translateX(-25px) rotateY(-90deg);
}


.Sheet {
    /* display: grid; */
    /* grid-template: repeat(3, auto) / repeat(3, auto);
    grid-gap: 20px; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
    width: 100%;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: absolute;
    pointer-events: none;
    
}

.Game .Sheet:nth-child(1) {
    -webkit-transform: translateZ(-135px);
            transform: translateZ(-135px);
    
  }
  
.Game .Sheet:nth-child(2) {
    -webkit-transform: translateZ(-25px);
            transform: translateZ(-25px);
  }
  
.Game .Sheet:nth-child(3) {
    -webkit-transform: translateZ(85px);
            transform: translateZ(85px);
  }


.GameInfo {
    text-align: center;
}

.awaitingOpponent {
    margin-top: 10px;
}

.awaitingOpponent p {
    font-size: 2em;
}

@media screen and (max-width: 420px) {
    .awaitingOpponent p {
         font-size: 1.5em;
    }
  }

.indicatorCubeDiv {
    margin-top: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.indicatorCubeWrapper {
    -webkit-perspective: 500px;
            perspective: 500px;
    -webkit-perspective-origin: 50% 50%;
            perspective-origin: 50% 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.indicatorCube {
    margin: auto;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    width: 30px;
    height: 30px;
    -webkit-animation: cubeSpin 5s linear infinite;
            animation: cubeSpin 5s linear infinite;
    position: relative;
    
}

@-webkit-keyframes cubeSpin {
    from {-webkit-transform: rotateY(0deg) rotateX(0deg);transform: rotateY(0deg) rotateX(0deg)}
    to {-webkit-transform: rotateY(360deg) rotateX(360deg);transform: rotateY(360deg) rotateX(360deg)}
  }

@keyframes cubeSpin {
    from {-webkit-transform: rotateY(0deg) rotateX(0deg);transform: rotateY(0deg) rotateX(0deg)}
    to {-webkit-transform: rotateY(360deg) rotateX(360deg);transform: rotateY(360deg) rotateX(360deg)}
  }
  


.indicatorCubeSide {
    background-color: rgba(252, 252, 142);
    opacity: .5;
    width: 100%;
    height: 100%;
    border-radius: 3px;
    border: rgb(0, 58, 0) solid 1px;
    position: absolute;
    pointer-events: all;
    transition: background-color 250ms;
    transition-delay: 500ms;
    box-shadow: 0px 0px 30px 2px yellow;
}

.indicatorCube .indicatorCubeSide:nth-child(1) {
    -webkit-transform: rotateY(0deg) translateZ(15px);
            transform: rotateY(0deg) translateZ(15px);
}

.indicatorCube .indicatorCubeSide:nth-child(2) {
    -webkit-transform: rotateY(90deg) translateZ(15px);
            transform: rotateY(90deg) translateZ(15px);
}

.indicatorCube .indicatorCubeSide:nth-child(3) {
    -webkit-transform: rotateY(180deg) translateZ(15px);
            transform: rotateY(180deg) translateZ(15px);
}

.indicatorCube .indicatorCubeSide:nth-child(4) {
    -webkit-transform: rotateY(-90deg) translateZ(15px);
            transform: rotateY(-90deg) translateZ(15px);
}

.indicatorCube .indicatorCubeSide:nth-child(5) {
    -webkit-transform: rotateX(90deg) translateZ(15px);
            transform: rotateX(90deg) translateZ(15px);
}
  
.indicatorCube .indicatorCubeSide:nth-child(6) {
    -webkit-transform: rotateX(-90deg) translateZ(15px) ;
            transform: rotateX(-90deg) translateZ(15px) ;
}




.ResetGame {
    margin-top: 100px;
    text-align: center;
}


.wrapper {
    -webkit-perspective: 1000px;
            perspective: 1000px;
    -webkit-perspective-origin: 50% 50%;
            perspective-origin: 50% 50%;
    margin-top: 100px;
}

.Game {
    width: 300px;
    height: 300px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: relative;
    margin: auto;
    -webkit-transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
            transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    pointer-events: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.winnerRotation {
    -webkit-animation: winnerSpin 15s linear infinite;
            animation: winnerSpin 15s linear infinite;
}

@-webkit-keyframes winnerSpin {
    from {-webkit-transform: rotateY(360deg) rotateX(360deg);transform: rotateY(360deg) rotateX(360deg)}
    to {-webkit-transform: rotateY(0deg) rotateX(0deg);transform: rotateY(0deg) rotateX(0deg)}
  }

@keyframes winnerSpin {
    from {-webkit-transform: rotateY(360deg) rotateX(360deg);transform: rotateY(360deg) rotateX(360deg)}
    to {-webkit-transform: rotateY(0deg) rotateX(0deg);transform: rotateY(0deg) rotateX(0deg)}
  }


.Setup{
    margin: auto;
    margin-top: 130px;
    text-align: center;
}

.setupOptions {
    margin-top: 70px;
}


body {
    font-family: 'Cutive Mono', monospace;
    transition: color 3s;
    overflow-x: hidden;
    overflow-y: hidden;
}

body button,
input[type=text] {
    font-family: 'Cutive Mono', monospace;
}

body button {
    font-size: 1.5em;
    margin: 5px;
    border-style: groove;
    background-color: rgba(252, 252, 142, 0.699)
}

body button:hover {
    background-color: rgba(252, 252, 142)
} 
